import React from 'react';
import { Popover, NavBar, Icon } from 'antd-mobile';

const _workout = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/_workout.svg`} alt=""/>;
const _rehab = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;
const _profile = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;
const _content = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;
const _ask = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;
const _faq = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;
const _logout = src => <img src={`../../Assets/MainMenuIcons/PopupMenuIcons/${src}.svg`} alt=""/>;

const Item = Popover.Item;

const myImg = src => <img src={`https://gw.alipayobjects.com/zos/rmsportal/${src}.svg`} className="am-icon am-icon-xs" alt="" />;

export default class PopoverMenu extends React.Component {
  state = {
    visible: false,
    selected: '',
  };
  onSelect = (opt) => {
    console.log(opt.props.value);
    this.setState({
      visible: false,
      selected: opt.props.value,
    });
  };
  handleVisibleChange = (visible) => {
    this.setState({
      visible,
    });
  };
  render() {
    return (<div className="main-menu-popovermenu">
          <Popover mask
            overlayClassName="fortest"
            overlayStyle={{ color: 'currentColor' }}
            visible={this.state.visible}
            placement='bottomLeft'
            overlay={[
              (<Item key="1" value="Workout" icon={<img src={'../../Assets/MainMenuIcons/PopupMenuIcons/_workout.svg'} className="am-icon am-icon-xs" alt="no"/>}>Workout</Item>),
            ]}
            align={{
              overflow: { adjustY: 0, adjustX: 0 },
              offset: [-10, 0],
            }}
            onVisibleChange={this.handleVisibleChange}
            onSelect={this.onSelect}
          >
            <img src={require('../../Assets/menu-icon.png')} height='100%' width='100%' alt="work" />
          </Popover>

    </div>
  );
  }
}



